@import url(https://fonts.googleapis.com/css2?family=Metal+Mania&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bod{text-align:center;padding:50px;margin:auto;background-image:linear-gradient(35deg, darkred, #230082);min-height:100vh;background-color:#01092f}.bod h1{color:red}.welcome{font-size:40pt;font-family:'Metal Mania', cursive;color:#fa3e3e}

.input-area{padding:30px}.input{width:300px;height:50px;font-size:15pt;position:relative;bottom:30px}

.monster{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));grid-gap:20px;justify-content:center;align-items:center;justify-items:center}.cardes{background-color:#61dafb;border-radius:15px;border:solid brown 5px;width:250px}.cardes:hover{transition:200ms;transform:scale(1.1);cursor:pointer}.cardes:active{-webkit-animation-name:click;animation-name:click;-webkit-animation-duration:300ms;animation-duration:300ms}@-webkit-keyframes click{50%{transform:scale(1)}}@keyframes click{50%{transform:scale(1)}}

